
import { createClient } from '@supabase/supabase-js';

// Supabase  
export const supabase = createClient(
  import.meta.env.VITE_PUBLIC_SUPABASE_URL,
  import.meta.env.VITE_PUBLIC_SUPABASE_ANON_KEY
);

// Edge Functions  
export const invokeEdgeFunction = async <T = any>(
  functionName: string,
  body?: any,
  options?: {
    retries?: number;
    timeout?: number;
  }
): Promise<T> => {
  const { retries = 3, timeout = 30000 } = options || {};
  
  for (let attempt = 1; attempt <= retries; attempt++) {
    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), timeout);
      
      const { data, error } = await supabase.functions.invoke(functionName, {
        body,
        signal: controller.signal
      });
      
      clearTimeout(timeoutId);
      
      if (error) {
        throw new Error(error.message || `Edge Function ${functionName}  `);
      }
      
      return data;
    } catch (error: any) {
      console.error(`Edge Function ${functionName}   ( ${attempt}/${retries}):`, error);
      
      if (attempt === retries) {
        throw error;
      }
      
      //  : 2^attempt * 1000ms
      const delay = Math.pow(2, attempt) * 1000;
      await new Promise(resolve => setTimeout(resolve, delay));
    }
  }
  
  throw new Error(`Edge Function ${functionName}     `);
};

//   Edge Functions 
export const edgeFunctions = {
  aiParseOrder: (inputText: string) => 
    invokeEdgeFunction<any>('ai-parse-order', { inputText }),
    
  ecountCreateOrder: (orderData: any) => 
    invokeEdgeFunction<any>('ecount-create-order', { orderData }),
    
  ecountSync: () => 
    invokeEdgeFunction<any>('ecount-sync')
};

//   
export class SupabaseError extends Error {
  constructor(
    message: string,
    public code?: string,
    public details?: any
  ) {
    super(message);
    this.name = 'SupabaseError';
  }
}

//   
export const handleSupabaseError = (error: any): never => {
  if (error.name === 'AbortError') {
    throw new SupabaseError('  .', 'TIMEOUT');
  }
  
  if (error.message?.includes('fetch')) {
    throw new SupabaseError('  .', 'NETWORK_ERROR');
  }
  
  throw new SupabaseError(
    error.message || '    .',
    error.code,
    error
  );
};
